.avatar img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

.wrapper {
  display: grid;
  grid-template-columns: 1fr 7fr;
  margin-left: 20px;
}

.leftSide {
  margin: 20px 0;
}

.rightSide {
  margin: 30px 0;
}

.name {
  font-weight: bold;
}

.selectedPage {
    font-weight: bold;
}